.App {
    background: linear-gradient(180deg, #27556F 55.5%, #000 100%);
    width: 100%;
    min-width: 1280px;
    height: 1280px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App-Long {
    background: linear-gradient(180deg, #27556F 55.5%, #000 100%);
    width: 100%;
    min-width: 1280px;
    height: 1615px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.TopBar {
    display: flex;
    flex-direction: row;
    width: 1280px;
}

.TopBar-row {
    display: flex;
    flex-direction: row;
}

.TopBar-spacer1 {
    flex: 164;
}

.TopBar-spacer2 {
    flex: 336;
}

.TopBar-spacer3 {
    flex: 86;
}

.LanguageSwitch {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-left: 16px;
    cursor: pointer;
}

.LanguageSwitch-text {
    color: #ffffff;
    font-size: 20px;
    margin-left: 12px;
}

.LanguageSwitch-text:hover {
    text-decoration: underline;
}

.TopButton {
    font-size: 24px;
    color: #ffffff;
    width: 127px;
    height: 65px;
    background: #40a2d8;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 33px;
    border-radius: 12px;
    cursor: pointer;
    z-index: 1;
    text-decoration: none;
}

.TopButton:hover, .TopButton:focus, .TopButton-select {
    background: #0b60b0;
    text-decoration: underline;
}

.TopButton-last {
    margin-right: 0;
}