.Story {
    width: 1280px;
    height: 1215px;
    background-image: linear-gradient(180deg, #27556F 0%, rgba(39, 85, 111, 0.00) 100%), url("../public/img_street.webp");
    background-size: 1280px 1215px;
}

.Story-CenterText {
    color: #ffffff;
