.HomeBanner {
    width: 1280px;
    height: 590px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url('../public/img_street_web.webp');
}

.HomeBanner-img {
    position: absolute;
    opacity: 80%;
    top: -20px;
}

.Title {
    background: linear-gradient(180deg, #FFA447 0%, #FFFC9B 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    z-index: 1;
    font-weight: bold;
}

.Title-JPN {
    font-size: 80px;
    margin-top: 32px;
}

.Title-ENG {
    font-style: italic;
    font-size: 40px;
}

.SubTitle {
    color: #ffffff;
    font-size: 36px;
}

.SubTitle-left {
    margin-top: auto;
    align-self: flex-start;
    margin-left: 32px;
}

.SubTitle-right {
    align-self: flex-end;
    margin-right: 32px;
    margin-bottom: 45px;
}

.Desc {
    color: #ffffff;
    font-size: 32px;
    margin-top: 28px;
    text-align: center;
}

.Desc-tips {
    font-size: 20px;
    opacity: 50%;
    margin-top: 22px;
}

.tips {
    font-size: 20px;
    color: #ffffff;
    opacity: 50%;
    margin-top: 36px;
    display: flex;
    justify-content: center;
}

.Available {
    display: flex;
    flex-direction: row;
    padding-left: 224px;
    padding-right: 224px;
    width: calc(1280px - 448px);
    justify-content: space-between;
    margin-top: 36px;
}

.Available-column {
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Available-spacer {
    flex: 1;
}

.Available-text-small {
    font-size: 16px;
    margin-bottom: 3px;
}

.Available-text-big {
    font-size: 36px;
}