.About {
    width: 1280px;
}

.About-Title-First {
    color: white;
    font-size: 48px;
    margin-top: 64px;
    text-align: center;
}

.About-Title {
    color: white;
    font-size: 48px;
    margin-top: 48px;
    text-align: center;
}

.About-Content {
    color: white;
    font-size: 24px;
    margin-left: 48px;
    margin-right: 48px;
    margin-top: 9px;
    white-space: pre-wrap;
}

.About-Content a {
    color: #fffc9b;
}

.About-Row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 12px;
}

.About-Column {
    text-align: center;
    margin-left: calc(112px / 2);
    margin-right: calc(112px / 2);
}

.About-IconDesc {
    width: 163px;
    font-size: 24px;
    color: white;
    text-align: center;
}

.About-Column-Kotlin {
    margin-top: -15px;
}

.About-IconDesc-Kotlin {
    margin-top: -15px;
}

.About-Column-React {
    margin-top: -15px;
}

.About-IconDesc-React {
    margin-top: -17px;
}

.About-Table {
    margin-left: 48px;
}

.About-Table td {
    text-align: center;
    padding-bottom: 8px;
    padding-top: 8px;
}

.About-Table tr {
    border: 1px none #ddd;
    border-bottom-style: solid;
}

.About-Table tr:first-child {
    border-bottom: none;
}

.About-Table tr:last-child {
    border-bottom: none;
}

.About-Notes {
    font-size: 20px;
    color: white;
    opacity: 0.5;
    margin-top: 16px;
    margin-left: 48px;
}