.Download-bg {
    position: absolute;
}

.Download-gradient {
    width: 1280px;
    height: 1215px;
    position: absolute;
    background: linear-gradient(180deg, #27556F 0%, rgba(39, 85, 111, 0.00) 100%);
}

.IconRow {
    display: flex;
    flex-direction: row;
    padding-left: 240px;
    padding-right: 240px;
    width: calc(1280px - 480px);
    justify-content: space-between;
    margin-top: 64px;
}

.IconColumn {
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
}

.IconColumn-text-small {
    font-size: 16px;
    margin-bottom: 3px;
}

.IconColumn-text-big {
    font-size: 36px;
}

table {
    border-collapse: collapse;
    font-size: 20px;
    z-index: 1;
}

table tr {
    border: 1px none #ddd;
    border-bottom-style: solid;
}

table tr:last-child {
    border-bottom-style: none;
}

table th {
    color: white;
    opacity: 50%;
    text-align: center;
    padding-top: 7px;
    padding-bottom: 7px;
    width: 230px;
}

table td {
    color: white;
    width: 282px;
}

.TableRow {
    width: 1280px;
    display: flex;
    flex-direction: row;
    margin-top: 27px;
}

.TableRow-spacer {
    flex: 1;
}

.TableRow-left {
    margin-left: 10px;
}

.TableRow-right {
    margin-right: 54px;
}

.Download-notes {
    position: relative;
    z-index: 1;
    white-space: pre-wrap;
    color: white;
    font-size: 20px;
    text-align: center;
}

.ButtonRow {
    position: relative;
    z-index: 1;
}

.ButtonRow span {
    color: white;
    font-size: 16px;
    margin-left: 8px;
}

.ButtonRow a {
    text-decoration: none;
}

.Button-Windows {
    background-color: #40a2d8;
    border-radius: 8px;
    width: 339px;
    height: 32px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 35px;
    margin-left: 134px;
    cursor: pointer;
}

.Button-Windows:hover {
    background-color: #0b60b0;
}

.Button-MacOS {
    position: absolute;
    right: 135px;
    top: 0;
    background-color: #0c2d57;
    border-radius: 8px;
    width: 339px;
    height: 32px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Button-MacOS:hover {
    background-color: black;
}

.Button-MacOS-Intel {
    top: 32px;
    margin-top: 12px;
}

.Download-desc {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 92px;
    font-size: 32px;
    color: #ffffff;
    width: 1113px;
    white-space: pre-wrap;
    text-align: center;
}

.Github-link {
    position: relative;
    margin-top: 22px;
    display: flex;
    justify-content: center;
    z-index: 1;
    font-size: 32px;
    color: #fffc9b;
}